* {
  margin: 0;
  padding: 0;
}
/*main-color:#ffbc00;
light-color:#f4f4f4
medium:#ccc;
dark:#333;*/
body {
  font-family: 'Dosis', sans-serif;
  line-height: 1.6;
  position: relative;
  min-height: 100vh;
  background: #fff;
}

a {
  text-decoration: none;
  color: white;
}
ul {
  list-style: none;
}

h2,
h3,
h4 {
  text-transform: uppercase;
}
img {
  width: 100%;
}
#logo {
  width: 70px;
  height: 70px;
  color: #fff;
  text-transform: uppercase;
}

.header-home {
  background: url('img/showcase.jpg') no-repeat center center/cover;
  height: 100vh;
  color: #fff;
}

.header-content {
  text-align: center;
  padding-top: 20%;
}

.container {
  max-width: 1260px;
  padding: 0 1.5rem;
  margin: auto;
}

.main-nav {
  display: flex;
  justify-content: space-between;
  padding-top: 1rem;
}
.main-nav ul {
  display: flex;
}
.main-nav ul li {
  padding: 1rem 1.5rem;
}

.main-nav ul li .Link {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  border-bottom: 3px transparent solid;
  padding-bottom: 0.5rem;
  transition: 0.5s;
}
.main-nav ul li .Link:hover {
  border-color: #ccc;
}
.main-nav ul li .current {
  border-color: #ffbc00;
}
.main-nav ul li .current:hover {
  border-color: #ffbc00;
}

.alert {
  padding: 0.7rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: #fff;
  color: #333;
}

.alert-primary {
  background: #dc3545;
  color: #fff;
}
.alert-light {
  background: #ffbc00;
  color: #fff;
}
.work {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.btn-main {
  display: inline-block;
  padding: 0.8rem 2rem;
  transition: 0.5s;
  border: none;
  cursor: pointer;
  color: #333;
  background-color: #ffbc00;
}

.btn-light {
  display: inline-block;
  padding: 0.8rem 2rem;
  transition: 0.5s;
  border: none;
  cursor: pointer;
  color: #333;
  background-color: #f4f4f4;
}
.btn-dark {
  display: inline-block;
  padding: 0.8rem 2rem;
  transition: 0.5s;
  border: none;
  cursor: pointer;
  color: #f4f4f4;
  background-color: #333;
}
.bio-image1:hover {
  transform: scale(1.1);
  opacity: 0.9;
}
.bg-main {
  background: #ffbc00;
  color: #ffbc00;
}
.bg-dark {
  background: #333;
  color: #333;
}
.bg-light {
  background: #f4f4f4;
  color: #f4f4f4;
}
.bg-medium {
  background: #ccc;
  color: #ccc;
}
.btn-light:hover,
.btn-dark:hover,
.btn-main:hover {
  background-color: #ffbc00;
}

.lead {
  font-size: 1.3rem;
  margin-bottom: 2rem;
}
.text-center {
  text-align: center;
}
.txt-type {
  border-right: 2px solid gray;
}

.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}
.py-4 {
  padding: 4rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}
.my-4 {
  margin: 4rem 0;
}

#home-a .specials {
  display: flex;
  padding-bottom: 5rem;
}

#home-a .specials i {
  color: #ffbc00;
}
.specials .Ui {
  flex: 1;
}
.specials .fullstack {
  flex: 1;
}

.bottom-line {
  height: 2px;
  width: 3rem;
  background: #ffbc00;
  display: block;
  margin: 0 auto 1rem auto;
}
/*about page*/
#header-inner {
  background: url('img/showcase.jpg') no-repeat 90% 50% / cover;
  height: 5.5 rem;
  border-bottom: 3px solid #ffbc00;
}

.about-logos {
  display: flex;
  height: 5rem;
  padding-bottom: 5rem;
  overflow: hidden;
  justify-content: space-evenly;
}
.about-logos img {
  width: 5rem;
  color: #333;
}
.padding-fix {
  padding-bottom: 5rem;
}
.about-info {
  display: flex;
  height: 15rem;
  margin-bottom: 1rem;
}
.about-info img {
  width: 33%;
  margin-right: 1rem;
}
.about-info #bio {
  border-left: 2px solid #ffbc00;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #333;
}
.about-info #bio p {
  padding: 0 3rem;
  margin-top: 2rem;
}
.about-skills {
  color: #333;
  background: #f4f4f4;
  display: flex;
  height: 15rem;
  margin-bottom: 1rem;
}
.about-skills .skills1 {
  padding: 1rem;
  width: 33%;
  margin-right: 1rem;
}
.about-skills .skills1 ul,
.about-skills .skills2 ul,
.about-skills .skills3 ul {
  margin-top: 1rem;
}
.about-skills .skills2 {
  padding: 1rem;
  width: 33%;
  margin-right: 1rem;
}
.about-skills .skills3 {
  padding: 1rem;
  width: 33%;
}
