  border-left: 2px solid #ffbc00;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #333;
} */

.flex-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.flex-row .text-input {
  width: 50%;
  margin: 0 0.5rem;
}
.flex-row1 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.flex-row1 .text-input {
  margin: 0 0.5rem;
  width: 50%;
}
textarea {
  width: 50%;
  height: 1rem;
}
textarea:hover {
  transition: 0.5s;
  border-color: #ffbc00;
}

input:hover {
  border-color: #ffbc00;
  transition: 0.5s;
}
.text-input {
  padding: 0.5rem 1rem;
  border: 1px solid #333;
}
#contact-a {
  padding-bottom: 6rem;
}

#main-footer {
  background: #333;
  position: absolute;
  bottom: 0;
  width: 100%;
  max-width: 100%;
  color: #fff;
  height: 5rem;
  bottom: 0;
}
#main-footer .footer-content {
  display: flex;
  justify-content: space-between;

  height: 5rem;
  align-items: center;
}
#main-footer .footer-content .social .fab {
  margin-right: 1rem;
  border: 2px #fff solid;
  border-radius: 50%;
  height: 20px;
  width: 20px;
  line-height: 20px;
  text-align: center;
  padding: 0.5rem;
}
#main-footer .footer-content .social .fab:hover {
  background: #ffbc00;
}
footer {
  margin-top: 1rem;
}

@media (max-width: 800px) {
  .header-home {
    height: 30rem;
  }
  .header-content {
    padding-top: 5rem;
  }
}
@media (max-width: 650px) {
  .about-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    height: 100%;
  }
  .about-info img {
    width: 100%;
    margin: 0;
    margin-bottom: 1rem;
  }
  .about-info #bio {
    border-left: 2px solid #ffbc00;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #333;
    height: 25rem;
  }
  .about-info .bio-image1 {
    width: 100%;
    margin: 0;
    height: 15rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  .about-info #bio p {
    padding: 0 3rem;
    margin-top: 2rem;
  }
}

/* smartphones */
@media (max-width: 420px) {
  .main-nav {
    flex-direction: column;
    align-items: center;
  }
  li {
    padding: 1rem;
  }
  .header-home {
    height: 10rem;
    border-bottom: 3px solid #ffbc00;
  }
  .header-content {
    display: none;
  }
  #header-inner {
    height: 10rem;
  }
  .about-info,
  .about-logos,
  .specials,
  .flex-row,
  .flex-row1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    height: 100%;
  }
  .fullstack {
    margin-top: 3rem;
  }
  .flex-row .text-input,
  .flex-row1 .text-input {
    margin-bottom: 1rem;
  }
  .about-info img {
    width: 100%;
    margin: 0;

    margin-bottom: 1rem;
  }
  .about-info #bio {
    border-left: 2px solid #ffbc00;
    display: flex;

    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #333;
    height: 25rem;
  }
  .about-info .bio-image1 {
    width: 100%;
    margin: 0;
    height: 15rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  .about-info #bio p {
    padding: 0 3rem;
    margin-top: 2rem;
  }
  .about-skills {
    margin: 0;
    padding: 0;
    color: #333;
    background: #f4f4f4;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
    margin-bottom: 1rem;
  }
  .about-skills .skills1 {
    padding: 1rem;
  }
  .about-skills .skills1 ul,
  .about-skills .skills2 ul,
  .about-skills .skills3 ul {
    margin-top: 1rem;
    line-height: 0.2;
  }
  .about-skills .skills1 ul li,
  .about-skills .skills2 ul li,
  .about-skills .skills3 ul li {
    line-height: 0;
    margin: 0;
  }
  .about-skills .skills2 {
    padding: 1rem;
  }
  .about-skills .skills3 {
    padding: 1rem;
  }
  .about-logos {
    display: flex;

    flex-direction: row;
    height: 100%;
  }
  .about-logos img {
    width: 2rem;
    padding: 1rem;
    color: #333;
  }
  #main-footer .footer-content {
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    height: 4rem;
    align-items: center;
  }
  #main-footer .footer-content .social .fab {
    margin-right: 1rem;
    border: 2px #fff solid;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    text-align: center;
    padding: 0.5rem;
  }
}
